  computed:{
    zoneFilterHandler(){
      let temp = this.Filter;
      let FilterContent = [];
      let ResultContent = [];
      let targetPage = (this.selPage - 1)*9;

      if(this.Filter === ''){
          for(let i=0;i<9;i++){
            if(this.Alldata[targetPage+i]){
              ResultContent.push(this.Alldata[targetPage+i])
            }
          }
        return ResultContent
      }
      else{
        FilterContent = this.Alldata.filter(item => {
          return item.Zone === temp
        })
        for(let i=0;i<9;i++){
          if(FilterContent[targetPage+i]){
            ResultContent.push(FilterContent[targetPage+i])
          }
        }
        return ResultContent
      }
    },
    pageLengthHandler(){
      let temp = this.Filter;

      if(this.Filter === ''){
        return this.Alldata.length
      }
      else{
        return this.Alldata.filter(item => {
          return item.Zone === temp
        }).length;
      }
    },
  },
  mounted(){
    this.$store.dispatch('init_index');
    this.testdatabase();
    this.getdata();
  }
