<template>
  <div id="allPage">

    <div id="pageArea">
      <nav aria-label="Page navigation example">
        <ul class="pagination">
          <li class="page-item off-sel" :class="{'not-active': nowPage === 1}" @click.prevent="pageChangeEmit(nowPage-1)">
            <a class="page-link" aria-label="Previous"  >
              <span aria-hidden="true">&lt;</span>
              <span class="sr-only">Previous</span>
            </a>
          </li>

          <select id="selArea" class="form-control" @change="pageSelEmit" v-model="selPage">
            <option v-for="index in totalPage" :key="index" :value="index">{{index}}</option>
          </select>

          <li class="page-item off-sel" :class="{'not-active': nowPage === totalPage}" @click.prevent="pageChangeEmit(nowPage+1)">
            <a class="page-link" aria-label="Next" >
              <span aria-hidden="true">&gt;</span>
              <span class="sr-only">Next</span>
            </a>
          </li>
        </ul>
      </nav>
    </div>

    <div id="textArea">
      {{nowPage}} <small>of</small> {{totalPage}}
    </div>



  </div>
</template>

<script>
export default {
  data(){
    return{
      selPage:1,
    }
  },
  props:{
    dataLength:{
      type:Number,
      required:true  
    },
    nowPage:{
      type:Number,
      required:true
    }
  },
  methods:{
    pageChangeEmit(val){
      this.$emit('pageChange',val)
    },
    pageSelEmit(e){
      let val = Number(e.target.value)
      this.$emit('pageChange',val)
    }
  },
  computed:{
    totalPage(){
      return Math.ceil(this.dataLength/9)
    },
  },
  watch:{
    nowPage(val){
      this.selPage = val
    }
  }
}
</script>

<style lang="scss" scoped>
.not-active {
  pointer-events: none;
  cursor: default;
  text-decoration: none;
  color: gray;
}

.page-item{
  cursor: pointer;
}

.off-sel{
  user-select: none;
}

#allPage{
  max-width: 400px;
  margin: 10px auto;
}

#selArea{
  max-width:60px;
}

#pageArea{
  max-width: 130px;
  margin: 0px auto;
}

#textArea{
  max-width: 130px;
  margin: 0px auto;
  text-align: center;
}
</style>