<template>
  <div id="allCard" class="col-12 col-md-6 col-xl-4">

    <div id="zoneCard" class="card">
      <img id="photo" class="card-img-top" height="300px" :src="editsrc(zone.Picture1)" :alt="zone.Picdescribe1">
      <div id="locationTitle">
        <h3>{{zone.Name}}</h3>
      </div>
      <ul class="list-group list-group-flush">
        <li v-if="zone.Opentime.length > 20" class="list-group-item" :class="{'not-active': isActiveA}">{{zone.Opentime | textFilter}}<a href="#" @click.prevent="moreInfoA"><small></small></a></li>
        <li v-else class="list-group-item">{{zone.Opentime}}</li>
        <li v-if="isActiveA" class="list-group-item">{{zone.Opentime}}</li>

        <li v-if="zone.Add.length > 20" class="list-group-item" :class="{'not-active': isActiveB}">{{zone.Add | textFilter}}<a href="#" @click.prevent="moreInfoB"><small></small></a></li>
        <li v-else class="list-group-item">{{zone.Add}}</li>
        <li v-if="isActiveB" class="list-group-item">{{zone.Add}}</li>
        
        <li v-if="zone.Tel.length > 20" class="list-group-item" :class="{'not-active': isActiveC}">{{zone.Tel | textFilter}}<a href="#" @click.prevent="moreInfoC"><small></small></a></li>
        <li v-else class="list-group-item">{{zone.Tel}}</li>
        <li v-if="isActiveC" class="list-group-item">{{zone.Tel}}</li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
    data(){
        return{
          isActiveA:false,
          isActiveB:false,
          isActiveC:false,
        }
    },
    props:{
        zone:{
            type:Object,
            required:true
        }
    },
    methods:{
      editsrc(data){
        let judge = data.substr(0,5);
        let edit = data.substr(4)
        if(judge !== 'https'){
          edit = 'https' + edit;
          return edit
        }else{
          return data
        }
      },
      moreInfoA(){
        this.isActiveA = !this.isActiveA;
      },
      moreInfoB(){
        this.isActiveB = !this.isActiveB;
      },
      moreInfoC(){
        this.isActiveC = !this.isActiveC;
      }
    },
    computed:{
    }
}
</script>

<style lang="scss" scoped>
#allCard{
 margin: 10px auto;
}

#zoneCard{
  margin: 0px auto;
}

#photo{
  position: relative;
}

#locationTitle{
  position: absolute;
  margin-top: 220px;
  padding: 10px;
  color: white;
  background-color: black;
  top:0;
  left:0;
  h3{
    margin: 0px auto;
    cursor: default;
  }
}

.card{
  text-align:left;
  max-width: 400px;
}
.card-img-overlay{
  margin-top: 200px;
  background: pink;
  color: white;
}
.not-active {
  pointer-events: none;
  cursor: default;
  text-decoration: none;
  color: gray;
  display:none;
}

</style>